* {
    margin: 0;
    padding: 0;
}

.container{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

#slider{
    width: 640px;
    height: 640px;
    overflow: hidden;
    box-sizing: border-box;
}

#slider img{
    width: 100%;
    height: 100%;
}

.button {
    padding: 3px 10px;
    font-size: 24px;
    color: #000;
    cursor: pointer;
    border-radius: 5px;
    outline: none;
    border: 2px solid #000;
}